@import "shared/assets/styles/var/breakpoints";

.popup_message {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    z-index: 10;
    background-color: var(--theme-color-fourth-opacity-4);
    padding: var(--gap-2);
    display: flex;
    align-items: center;
    justify-items: center;

    @media (min-width: $md-md) {
        padding: var(--gap-3);
    }
    &_enter {
        opacity: 0;
        
        & .popup_message__container {
            opacity: 0;
            transform: translateY(20rem);
        }
    }

    &_enter_active {
        opacity: 1;
        transition: opacity 300ms;

        & .popup_message__container {
            opacity: 1;
            transform: translateY(0);
            transition: opacity 300ms, transform 300ms;
        }
    }

    &_exit {
        opacity: 1;

        & .popup_message__container {
            opacity: 1;
            transform: translateY(0);
        }
    }

    &_exit_active {
        opacity: 0;
        transition: opacity 300ms;

        & .popup_message__container {
            opacity: 0;
            transform: translateY(-20rem);
            transition: opacity 300ms, transform 300ms;
        }
    }

    &__container {
        max-width: 60rem;
        margin: 0 auto;
        width: 100%;

        @media (min-width: $md-md) {
            padding: var(--gap-3);
        }
    }

    &__alert {
        border-radius: var(--border-radius);
        padding: 4rem var(--gap-3);
        background-color: var(--theme-color-secondary);
        position: relative;
        width: 100%;
        color: #fff;
        font-size: var(--text-size-3);
        text-align: center;
    }

    &__close {
        position: absolute;
        top: 0.5rem;
        right: 0.5rem;
        height: 3rem;
        color: #fff;
        background-color: transparent;
        border: none;
        border-radius: 0;
        outline: none;
        padding: 0;
        & svg {
            height: 100%;
            width: auto;
        }
    }
}
