@import "shared/assets/styles/mixins";

.members {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    background-color: var(--theme-color-primary-opacity-4);
    padding: var(--gap-2);
    border-radius: var(--border-radius);

    &_enter {
        opacity: 0;
        transform: translateX(-100%);
    }

    &_enter_active {
        opacity: 1;
        transform: translateX(0%);
        transition: opacity .3s ease, transform .3s ease;
    }

    &_exit {
        opacity: 1;
        transform: translateX(0%);
    }

    &_exit_active {
        opacity: 0;
        transform: translateX(-100%);
        transition: opacity .3s ease, transform .3s ease;
    }

    &__body {
        background-color: var(--theme-color-fourth);
        padding: var(--gap-2);
        border-radius: var(--border-radius);
        height: 100%;
        width: calc(100% - 5rem);
        box-shadow: var(--shadow-default);
    }

    &__head {
        & button {
            width: 100%;
        }
    }

    &__list {
        margin-top: var(--gap-2);
        display: flex;
        flex-direction: column;
        gap: var(--gap-2);
        height: calc(100% - 4.6rem - var(--gap-2));
        overflow-y: auto;
        @include scroll;
    }
}

.member_card {
    display: flex;
    gap: var(--gap-2);
    align-items: center;

    &__photo {
        flex: 0 0 auto;
        width: 4rem;
    }

    &__delete {
        margin-left: auto;
        height: 3rem;
    }
}