@import "shared/assets/styles/var/breakpoints";

.switcher {
    display: flex;
    align-items: center;
    gap: var(--gap-1);
    font-size: var(--text-size-2);
    height: 100%;
    cursor: pointer;

    @media (min-width: $md-md) {
        font-size: var(--text-size-3);
    }

    &__lang {
        opacity: 0.5;

        &.active {
            opacity: 1;
        }
    }
}