@import "shared/assets/styles/var/breakpoints";

.header {
    background-color: var(--theme-color-secondary);
    padding: var(--gap-2);
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: var(--gap-3);
    color: var(--text-color-light);
    min-height: 10rem;

    @media (min-width: $md-md) {
        min-height: 11.5rem;
        padding: var(--gap-3);
    }
}

.left {

}
.avatar {
    width: 6rem;
    & a {
        color: inherit;
        text-decoration: none;
    }
}

.right {
    display: flex;
    align-items: center;
    gap: var(--gap-3);
}

.right_item {
    color: var(--text-color-light);
    height: 3rem;

    @media (min-width: $md-md) {
        height: 4rem;
    }
    & a {
        color: inherit;
    }
}

.header_icon {
    width: auto;
    height: 100%;
}