@import "shared/assets/styles/mixins";

.message_form {
    display: flex;
    align-items: center;
    gap: var(--gap-2);

    &__input_wrap {
        background-color: var(--theme-color-fourth);
        border-radius: var(--border-radius);
        padding: var(--gap-2);
        width: calc(100% - 4rem - var(--gap-2));
    }

    &__input {
        min-height: calc(4rem - (var(--gap-2) * 2));
        word-break: break-word;
        max-height: calc(10.8rem - (var(--gap-2) * 2));
        overflow-y: auto;
        width: 100%;
        @include scroll;

        &:focus-visible {
            border: none;
            outline: none;
            box-shadow: none;
        }

    }
    &__btn {
        flex: 0 0 auto;
        width: 4rem;
        height: 4rem;
        color: var(--theme-color-fourth);
        background-color: var(--theme-color-secondary);
        border-radius: var(--border-radius);
        padding: var(--gap-1);
        display: flex;
        align-items: center;
        justify-content: center;
        transition: transform var(--duration-and-timing-func);

        & svg {
            width: 100%;
            height: auto;
        }

        &:active {
            transform: scale(0.98);
        }
    }
}
