.profile_current_photo {
    & img {
        width: 100%;
        max-width: 25.6rem;
        height: auto;
        border-radius: var(--border-radius);
    }
}

.dropzone {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: var(--gap-3);
    border: 2px dashed var(--theme-color-primary);
    border-radius: var(--border-radius);
    min-height: 6rem;
}

.preview {
    display: flex;
    flex-wrap: wrap;
    gap: var(--gap-2);

    &__item {
        font-size: var(--text-size-1);
        text-align: center;
        word-break: break-word;

        & img {
            display: block;
            width: 10rem;
            height: 10rem;
            object-fit: cover;
            margin-bottom: var(--gap-2);
        }
    }
}
