@import "shared/assets/styles/var/breakpoints";
.dropDown {
    position: relative;
    z-index: 5;
    height: 100%;
    cursor: pointer;
    height: 100%;

    &.open {
        & .dropDown__icon {
            background-color: var(--theme-color-fourth-opacity-5);
        }

        & .dropDown__list {
            pointer-events: auto;
            visibility: visible;
            opacity: 1;
            transform: scale(1);
        }
    }

    &__icon {
        height: 100%;
        border-radius: 50%;
        transition: background-color var(--duration-and-timing-func);

        @media #{$mouse-device} {
            &:hover {
                background-color: var(--theme-color-fourth-opacity-5);
            }
        }
        & svg {
            height: 100%;
            width: auto;
        }
    }

    &__list {
        position: absolute;
        top: calc(100% + var(--gap-1));
        right: 0;
        border-radius: var(--border-radius);
        background-color: var(--theme-color-fourth);
        box-shadow: var(--shadow-default);
        padding: var(--gap-3);
        color: var(--text-color-dark);
        display: flex;
        flex-direction: column;
        gap: var(--gap-2);
        white-space: nowrap;
        text-align: right;
        justify-content: flex-end;
        align-items: flex-end;
        pointer-events: none;
        visibility: hidden;
        transform: scale(0.8);
        opacity: 0;
        transition: transform var(--duration-and-timing-func), opacity var(--duration-and-timing-func);
        
        & > * {
            border: none;
            padding: 0;
            background-color: transparent;
            display: inline;
            font-size: inherit;
            line-height: inherit;
            text-decoration: none;
        }
    } 
}
