@import "shared/assets/styles/var/breakpoints";

.input {
    display: flex;
    width: 100%;
    outline: none;
    border: none;
    min-height: 4rem;
    color: var(--text-color-dark);
    padding: 1rem var(--gap-3);
    border-radius: var(--border-radius);
    font-size: var(--text-size-2);

    @media (min-width: $md-md) {
        min-height: 4.6rem;
    }

    &::placeholder {
        transition: opacity .15s ease;
    }
    &:focus::placeholder {
        opacity: 0;
    }

    &.light {
        background-color: var(--theme-color-fourth);
    }
    
    &.dark {
        background-color: var(--theme-color-tertiary);
    }
    
    &.error {
        border: 2px solid var(--theme-color-error);
        color: var(--text-color-error);
    }
    
    &.error::placeholder {
        color: var(--text-color-error);
    }
}

.input_password {
    position: relative;

    & .input {
        padding-right: var(--gap-4);
    }
}

.input_password_icon {
    position: absolute;
    z-index: 5;
    height: 2rem;
    max-height: 80%;
    top: 50%;
    right: var(--gap-2);
    transform: translateY(-50%);

    & svg {
        height: 100%;
        width: auto;
    }
}