.card {
    display: block;
    color: var(--text-color-dark);
    text-decoration: none;
    animation: fadeIn .3s ease forwards;

    &__body {
        display: flex;
        gap: var(--gap-3);
        align-items: center;
    }

    &__photo {
        width: 7rem;
        flex: 0 0 auto;
    }

    &__text {
        flex: 1 1 auto;
    }

    &__name {
        font-size: var(--text-size-3);
    }

    &__options { 
        flex: 0 0 auto;
        height: 4rem;
    }

    @keyframes fadeIn {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
}