@import "shared/assets/styles/var/breakpoints";

.profile {

    @media (min-width: $md-md) {
        display: flex;
        gap: var(--gap-3);
    }

    &__head {

    }
    &__photo {
        height: 20rem;
        overflow: hidden;
        border-radius: var(--border-radius);
        cursor: pointer;

        & img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            object-position: top;
        }
    }

    &__body {
        position: relative;

        @media (min-width: $md-md) {
            min-width: 40rem;
        }
    }
    &__conf {
        position: absolute;
        top: 0;
        right: 0;
        width: 3rem;
        color: var(--text-color-primary);

        & svg {
            width: 100%;
            height:  auto;
        }
    }

    &__name {
        padding-right: 5rem;
    }

    &__contacts {
        border-collapse: collapse;
        font-size: calc(var(--text-size-1) * 1.2);
        & td:first-child {
            padding-right: var(--gap-2);
        }
    }
}