@import "shared/assets/styles/mixins";

.messages {
    background-color: var(--theme-color-secondary-opacity-2);
    border-radius: var(--border-radius);
    height: 100%;
    box-shadow: 0px 0px 2rem inset var(--theme-color-secondary-opacity-5);
    display: flex;
    flex-direction: column;
    padding: var(--gap-2);
    position: relative;

    &__members_toggle_btn {
        position: absolute;
        top: var(--gap-2);
        right: var(--gap-2);
        z-index: 4;
        border-radius: var(--border-radius);
        padding: var(--gap-1);
        background-color: var(--theme-color-fourth);
        box-shadow: 0px 0px 0.5rem var(--theme-color-secondary-opacity-8);
        display: flex;
        align-items: center;
        justify-content: center;
        & svg {
            height: 3rem;
            width: auto;
        }
    }

    &__list {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        //justify-content: flex-end;
        gap: var(--gap-2);
        height: calc(100% - 4.8rem);
        overflow-y: auto;
        margin: 0 -0.4rem;
        padding: 0 0.4rem 0.4rem;
        @include scroll;

        & > *:first-child {
            margin-top: auto;
        }
    }
    &__footer {
        padding-top: var(--gap-2);
    }
}
