.message {
    background-color: var(--theme-color-fourth);
    border-radius: var(--border-radius) var(--border-radius) var(--border-radius) 0;
    padding: var(--gap-2);
    font-size: var(--text-size-1);
    max-width: 90%;
    align-self: flex-start;
    box-shadow: 0px 0px 5px var(--theme-color-secondary-opacity-8);

    &.active {
        box-shadow: 0px 0px 10px var(--theme-color-secondary-opacity-9);

    }

    &__head {
        display: flex;
        gap: var(--gap-2);
        align-items: center;
    }
    &__photo {
        flex: 0 0 auto;
        width: 2rem;
    }
    &__date {
        color: var(--theme-color-primary-opacity-4);
    }
    &__content {
        margin-top: var(--gap-2);
    }

    &.owner {
        align-self: flex-end;
        border-radius: var(--border-radius) var(--border-radius) 0 var(--border-radius);
        background-color: #7be5ff;
        & .message__head {
            flex-direction: row-reverse;
        }
    }
}

.modal {
    position: absolute;
    z-index: 3;
    top: var(--gap-2);
    left: 50%;
    transform: translateX(-50%);
    max-width: 100%;
    display: inline-flex;
    padding:  var(--gap-2);
    background-color: var(--theme-color-fourth);
    box-shadow: 0px 0px 5px var(--theme-color-secondary-opacity-8);
    border-radius: var(--border-radius);

    &_enter {
        opacity: 0;
        transform: translateX(-50%) translateY(-50%);
    }

    &_enter_active {
        opacity: 1;
        transform: translateX(-50%) translateY(0%);
        transition: opacity .3s ease, transform .3s ease;
    }

    &_exit {
        opacity: 1;
        transform: translateX(-50%) translateY(0%);
    }
    &_exit_active {
        opacity: 0;
        transform: translateX(-50%) translateY(-50%);
        transition: opacity .3s ease, transform .3s ease;
    }

    & svg {
        height: 4rem;
        width: auto;
    }
}
